import { Component } from '@angular/core';
import { APP_ROUTE } from '@core/constants';
import { MatDialogModule } from '@angular/material/dialog';
import { SharedModule } from '@shared/shared.module';
import { Router } from '@angular/router';

@Component({
    selector: 'findqo-limit-reached-dialog',
    templateUrl: './limit-reached-dialog.component.html',
    styleUrl: './limit-reached-dialog.component.scss',
    imports: [MatDialogModule, SharedModule]
})
export class LimitReachedDialogComponent {
	constructor(private router: Router) {}
	onContinue() {
		this.router.navigate([APP_ROUTE.placeAdCategory], {
			queryParams: { sellerTypeId: 2 },
		});
	}
	onUpgrade() {
		this.router.navigate([APP_ROUTE.agentHub.businessAccount], {
			queryParams: { pricing: true },
		});
	}
}
